.event-item {
  display:flex;
  flex-wrap:wrap;
  width:100%;
  margin-bottom:$size-m;
  position:relative; 
  .event-item-section {
    padding:$size-m $size-l;
    flex:1 0;
  }
  .event-poster {
    flex:0 0 $poster-width-desktop;
    padding:0;
    position:relative;
    .icon {
      width:7rem;
      height:7rem;
      left:50%;
      top:50%;
      margin-top:-3.5rem;
      margin-left:-3.5rem;
      font-size:7rem;
      color:#FFF;
      border-radius:100%;
      border:2px solid #FFF;
      position:absolute;
      box-shadow:0 0 4px #000;
    }
  }
  .event-title {
    flex:0 0 35%;
    width:35%;
    overflow:hidden;
    padding-top:0;
    h2 {
      font-size:$size-xxl;
      font-weight:900;
      margin:0 0 $size-xs 0;
      input {
        padding:0;
        font-weight:900;
        margin:0;
        border:0;
        height:6rem;
        &:focus {
          outline:0
        }
      }
    }
    .event-type {
      font-size:$size-l;
      height:$size-l;
      margin-bottom: $size-xs;    
      font-weight:300;
    }
    .event-year {
      font-size:$size-m;
      height:$size-m;
      font-weight:300;
    }
  }
  .event-time {
    background:#f5f5f5;
    flex-grow: 1.5;
    a {
      font-size:$size-xl;
      font-weight:600;
      display:block;
      span {
        font-size:$size-l;
        margin-bottom:$size-s;
        font-weight:300;
        display:block;
      }
    }    
  }
  .event-tickets {
    border-right:1px solid #DDD;
    font-size:$size-l;
    .icons-seats {
      font-weight:600;
      margin-top:$size-s;
      > span {
        font-size:$size-xl;
        vertical-align: text-bottom;
      }
    }
  }
  .event-room {
    text-align:right;
    a {
      font-size:$size-xl;
      font-weight:600;
      display:block;
      span {
        font-size:$size-l;
        font-weight:300;
        display:block;
      }
    } 
  }
  .event-actions {
    opacity:0;
    position:absolute;
    right:15px;
    bottom:15px;
    a {
      display:block;
      color:$color-danger;
    }
  }
  &.playing, &.finished {
    opacity:0.4
  } 
  &:hover {
    .event-actions {
      opacity:1;
    }
  }
 
  @media (max-width: $mobile-breakpoint) {
    &::after {
      content: '';
      width: 100%;
      order: 1;
      border-top:1px solid #DDD;
    }
    .event-item-section {
      padding:$size-s;
    }
    .event-poster {
      padding:0;
      flex-basis: $poster-width-mobile;
    }
    .event-title {
      flex:0 0 calc(100% - #{$poster-width-mobile});
      width:calc(100% - #{$poster-width-mobile});
    }
    .event-time {
      order:2;
      flex-grow:3;
      padding-bottom:0;
      a span {
        margin-bottom:$size-xs;
      }
    }
    .event-tickets {
      order:3;
      flex-grow:2;
    }
    .event-room {
      order:4;
      padding-bottom:0;
    }
    .event-actions {
      bottom:auto;
      top:15px;
      opacity:1;
    }
  }
}
